@font-face {
  font-family: 'Montserat';
  src: url("./assets/Montserrat/Montserrat-VariableFont_wght.ttf")
}

body {
  margin: 0;
  font-family: 'Montserat';
  background-color: black;
}

a {
  color: #fff563;
  text-decoration: none;
}


iframe {
  width: 1200px;
  height: 950px;
  display: flex;
  margin: auto;

}

.dropdown-content {
  background-color: black;
}


.dropdown-trigger {
  background-color: #fff563;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  outline: none;
  transition: 0.2s all;
  text-align: center;
  color: black;
  font-size: 30px;
  font-weight: 900;
  font-family: 'Montserat';


}

.active,
.accordion:hover {
  background-color: black;
  color: #fff563
}

.header {
  width: 100%;
  height: 720px;
  background-size: cover;
  color: white;
  font-size: 56px;
  text-align: center;

}

.header-content {
  display: flex;
  justify-content: center;
}

.block-container {
  display: flex;
  width: 800px;
  margin: auto;
  justify-content: space-between;
}

.caption {
  font-size: 26px;
  font-weight: 600;
  font-family: 'Montserat'
}

.stream {
  width: 200px;
  height: 120px;
}

.title {
  color: #fff563;
  font-family: 'Montserat';
  font-weight: 900;
  display: flex;
  justify-content: space-between;
}

.watch-here {
  text-align: center;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 900;
}